body {
  background-color: #f7fff7;
  font-family: "Roboto", sans-serif;
}

.btn {
  color: #1a535c;
  background-color: #4ecdc4;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 2.5px;
  cursor: pointer;
}

input[type="text"],
input[type="email"],
input[type="date"],
input[type="password"],
input[type="tel"],
select,
textarea,
input {
  padding: 0.5rem;
  border-radius: 2.5px;
  border: 1px solid gray;
  box-sizing: border-box;
  width: 100%;
}

.form {
  background-color: white;
  border: 1px solid lightgray;
  border-radius: 25px;
  padding: 2rem;
  margin: 2rem auto;
  width: 20rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.form .input-error {
  color: #ff6b6b;
}

.form section {
  margin: 0.5rem 0;
  width: 100%;
}

.category-container {
  display: flex;
  grid-gap: 5rem;
  gap: 5rem;
  margin: 1rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.category {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border: 1px solid lightgray;
  border-radius: 50px;
  overflow: hidden;
  padding: 1rem 1.5rem;
  grid-gap: 1rem;
  gap: 1rem;
  color: #1a535c;
  height: 10rem;
}

.category .category-img {
  width: 15rem;
}

.category .border {
  width: 1px;
  background-color: gray;
}

.add-item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 4rem;
  text-decoration: none;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  margin: 5rem 0;
  font-size: 2rem;
}

.add-item span {
  color: #ff6b6b;
  font-size: 2rem;
}

.main-nav {
  width: 100%;
  background-color: #1a535c;
  border-bottom: 1px solid lightgray;
  padding: 1.5rem 1rem;
  margin: 0;
  list-style: none;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  grid-gap: 1rem;
  gap: 1rem;
  height: 4rem;
}

.main-nav .auth {
  margin-left: auto;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}

.main-nav .link {
  text-decoration: none;
  color: white;
}

.main-nav .btn {
  color: #1a535c;
}

/* .main-nav .auth .link {
  color: white;
  background-color: #4ecdc4;
  padding: 0.5rem 1rem;
  border-radius: 2.5px;
} */

.post-container {
  width: 40vw;
  margin: 6rem auto;
  list-style: none;
}

.post-container .post-item {
  background-color: white;
  border: 1px solid lightgray;
  border-radius: 10px;
  padding: 1rem 3rem;
  margin: 2rem 0;
  display: flex;
  color: black;
}

.post-container .post-item .post-info {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  font-size: 1.5rem;
}

.post-container .post-item .post-title {
  display: flex;
  flex-direction: column;
  font-size: 2rem;
  margin: 0 0 1.5rem 0;
}

.post-container .post-item .post-title span {
  font-size: 0.9rem;
  color: gray;
  display: flex;
  flex-direction: column;
  padding: 0.5rem 0;
}

.post-container .post-item span {
  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
}

.post-container .post-item img {
  width: 30%;
  padding: 1rem 0;
  /* margin: 2rem auto; */
}

.rent-post {
  display: flex;
  width: 70vw;
  margin: 7rem auto;
  background-color: white;
  padding: 3rem;
  border-radius: 10px;
  border: 1px solid lightgray;
}

.rent-post * {
  width: 50%;
}

.rent-post .post-info {
  font-size: 2rem;
  display: flex;
  justify-content: flex-start;
  /* align-items: center; */
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

.rent-post .post-info .rent-btn {
  margin: 0 auto;
  margin-top: auto;
}

.search-form {
  width: 90%;
  grid-gap: 1rem;
  gap: 1rem;
  background-color: white;
  border: 1px solid lightgray;
  border-radius: 10px;
  margin: 3rem auto;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3rem 2rem;
  flex-wrap: wrap;
}

.search-form input,
.search-form select {
  width: auto;
}

.contact-us {
  width: 40vw;
  margin: auto;
  margin-top: 5rem;
}

.contact-us p {
  font-size: 2rem;
}

.contact-us ul {
  list-style: none;
}

.contact-us a {
  color: darkblue;
  font-size: 1.5rem;
}

.profile-container {
  width: 30rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: auto;
  margin-top: 5rem;
}

.profile-container .user-data {
  /* background-color: red; */
  background-color: white;
  border: 1px solid lightgray;
  border-radius: 10px;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profile-container .profile-image {
  width: 12rem;
}

.profile-actions {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.profile-actions .btn {
  margin: 1rem;
}

.post-page .post {
  width: 50vw;
  height: 25rem;
  background-color: white;
  border: 1px solid lightgray;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 7rem auto;
  grid-gap: 2rem;
  gap: 2rem;
}

.post-page .post > * {
  width: 50%;
}

.post-page .post .post-img {
  /* overflow: hidden; */
  height: 100%;
}

